import {mapState} from "vuex";

export default {
    data() {
        return {
            open: false,
            list: [],
            config: []
        }
    },
    computed: {
        ...mapState({
            mall: state => state.mallConfig.mall,
            custom_setting: state => state.mallConfig.share_setting_custom,
            share_setting: state => state.mallConfig.share_setting,
        })
    },
    methods: {
        toCash() {
            uni.navigateTo({
                url: '/pages/share/cash/cash?money=' + this.list.money
            });
        },

        toDetail() {
            uni.navigateTo({
                url: '/pages/share/cash-detail/cash-detail'
            });
        },

        setting() {
            let that = this;
            that.$request({
                url: that.$api.share.setting,
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.config = response.msg.config;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
    },

    onLoad() { this.$commonLoad.onload();
        let that = this;
        uni.setNavigationBarTitle({
            title: that.custom_setting.menus.money.name
        });
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.$request({
            url: that.$api.share.brokerage,
        }).then(response=>{
            if(response.code == 0) {
                that.list = response.data.list;
                that.setting();
            }else {
                uni.showToast({
                    title: response.msg,
                    icon: 'none',
                    duration: 1000
                });
            }
        }).catch(response => {
            that.$hideLoading();
        });
    }
}
